<template>
  <div class="main-div">
    <div class="main-bg">
      <div class="compass-1-locate-box">
        <img class="compass-1-locate" src="@/assets/images/commute/commute-small-locate.png" alt="ghost">
      </div>
      <div style="bottom: 80px;" class="contain">
        <div class="description-content">
          <p class="description">怪奇佈下結界來隱藏自己，使我們沒辦法知道他們的精確位置。結界都會有三個端點，只要你盡量靠近「定位點」，正確擺放端點到羅盤上，取得破解結界的密碼，即可進入結界。博物館這邊會計算出三個端點，請速至地圖上的「定位點」附近待命。</p>
        </div>
        <div style="height: 60px;" class="arriveButton" @click="arriveButtonPressed">
          <p class="button-title">已抵達</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommuteArrivePositionPoint',
  mounted () {
    this.$store.commit('changeHeader', true)
  },
  methods: {
    arriveButtonPressed () {
      this.$router.push({name: 'CommuteAnswerCompassPosition'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/arrive-position-point.png');
      position: relative;

      .compass-1-locate-box {
        text-align: center;
        padding-top: 15vh;
      }

      .compass-1-locate {
        width: 45vw;
        height: auto;
      }

      .contain {
        position: absolute;
        bottom: $bottomSpace;
        .description-content {
          .description {
            @extend descriptionWhiteText;
            margin: 25px;
          }
        }
        .arriveButton {
          @extend vhCenter;
          height: $buttonHeight;
          background-color: $whiteButtonColor;
          .button-title {
            @extend buttonBlackText;
          }
        }
      }
    }
  }
</style>



